import LineGradient from "../components/LineGradient";
import useMediaQuery from "../hooks/useMediaQuery";
import { motion } from "framer-motion";

const MySkills = () => {
    const isAboveMediumScreens = useMediaQuery("(min-width: 1060px)");

    return (
        <section id="skills" className="pt-0 pb-24">
            {/* HEADER IMAGE SECTION */}
            <div className="md:flex md:justify-between md:gap-16 mt-32">
                <motion.div
                    className="md:w-1/3"
                    initial="hidden"
                    whileInView="visible"
                    viewport={{ once: true, amount: 0.5}}
                    transition={{ duration: 0.5}}
                    variants={{
                        hidden: { opacity: 0, x: -50 },
                        visible: { opacity: 1, x: 0}
                    }}
                >
                   <p className="font-playfair font-semibold text-4xl mb-5">
                    My <span className="text-red">SKILLS</span>
                    </p> 
                    <LineGradient width="w-1/3" />
                    <p className="mt-10 mb-7">
                    An Undergraduate Student with Web development and Data Structures and Algorithms knowledge, 
                    coupled with a passion for coding, 
                    possessing a wide range of skills that are highly valuable in the tech industry.
                    </p>
                </motion.div>
                <div className="mt-16 md:mt-0">
                    {isAboveMediumScreens ? (
                        <div className="relative z-0 ml-20 before:absolute before:-top-10 before:-left-10 
                            before:w-full before:h-full before:border-2 before:border-blue before:z-[-1]
                    ">
                        <img className="z-10"
                            alt="skills" src={require('../assets/skills-image.png')} />
                    </div>
                    ) : (
                        <img className="z-10"
                            alt="skills" src={require('../assets/skills-image.png')} />
                    )}
                </div>
            </div>

            {/* SKILLS */}
            <div className="md:flex md:justify-between mt-16 gap-32">
                {/* EXPERIENCE */}
                <motion.div
                    className="md:w-1/3 mt-10"
                    initial="hidden"
                    whileInView="visible"
                    viewport={{ once: true, amount: 0.5}}
                    transition={{ duration: 0.5}}
                    variants={{
                        hidden: { opacity: 0, y: 50 },
                        visible: { opacity: 1, y: 0}
                    }}
                >
                    <div className="relative h-32">
                        <div className="z-10">
                            <p className="font-playfair font-semibold text-5xl">01</p>
                            <p className="font-playfair font-semibold text-3xl mt-3">Experience</p>
                        </div>
                        <div className="w-1/2 md:w-3/4 h-32 bg-blue absolute right-0 top-0 z-[-1]" />
                    </div>
                    <p className="mt-5">
                        <span className="font-playfair font-semibold text-xl mt-3">Business Development Intern</span>
                        {/* <br></br>Designed several ideas for company’s logo and poster.
                        <br></br>Researched and analyzed Car sales data and maintained record. */}
                    </p>
                </motion.div>

                {/* WEB DEV */}
                <motion.div
                    className="md:w-1/3 mt-10"
                    initial="hidden"
                    whileInView="visible"
                    viewport={{ once: true, amount: 0.5}}
                    transition={{ delay: 0.2, duration: 0.5}}
                    variants={{
                        hidden: { opacity: 0, y: 50 },
                        visible: { opacity: 1, y: 0}
                    }}
                >
                    <div className="relative h-32">
                        <div className="z-10">
                            <p className="font-playfair font-semibold text-5xl">02</p>
                            <p className="font-playfair font-semibold text-3xl mt-3">Web Development</p>
                        </div>
                        <div className="w-1/2 md:w-3/4 h-32 bg-red absolute right-0 top-0 z-[-1]" />
                    </div>
                    <p className="mt-5">
                    <span className="font-playfair font-semibold text-xl mt-3">HTML, ReactJS, Tailwind CSS, Framer-Motion</span>
                    {/* <br></br>Proficiency in web development can open up a wide range of career opportunities. */}
                    </p>
                </motion.div>

                {/* Competitive Programming */}
                <motion.div
                    className="md:w-1/3 mt-10"
                    initial="hidden"
                    whileInView="visible"
                    viewport={{ once: true, amount: 0.5}}
                    transition={{ delay: 0.4, duration: 0.5}}
                    variants={{
                        hidden: { opacity: 0, y: 50 },
                        visible: { opacity: 1, y: 0}
                    }}
                >
                    <div className="relative h-32">
                        <div className="z-10">
                            <p className="font-playfair font-semibold text-5xl">03</p>
                            <p className="font-playfair font-semibold text-3xl mt-3">Competitive Coding</p>
                        </div>
                        <div className="w-1/2 md:w-3/4 h-32 bg-yellow absolute right-0 top-0 z-[-1]" />
                    </div>
                    <p className="mt-5">
                    <span className="font-playfair font-semibold text-xl mt-3">C, C++, Pyhton, Java</span>
                    {/* <br />Competitive coding improves problem-solving skills, increases coding speed, 
                    exposes individuals to new ideas and techniques, and can create career opportunities. */}
                    </p>
                </motion.div>
            </div>
        </section>
    )
}

export default MySkills;